body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ul {
  list-style: none;
  padding: 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

textarea {
  resize: none;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
  margin: 0 auto;
}

.no-seleccionar {
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none;
}

.alinear-derecha {
  margin-top: 2rem;
  display: flex;
  justify-content: flex-end;
}

.texto-justificado {
  text-align: justify;
}

.texto-mayusculas {
  text-transform: uppercase;
}

.w-100 {
  width: 100%;
}

.dark-mode-all {
  background-color: #1a1a1a;
}
.dark-mode-all p, .dark-mode-all a, .dark-mode-all h1, .dark-mode-all h2, .dark-mode-all h3, .dark-mode-all h4, .dark-mode-all td {
  color: #FFFFFF;
}
.dark-mode-all .anuncio {
  background-color: #1a1a1a;
  border: 1px solid #333333;
}
.dark-mode-all li img {
  filter: invert(100%);
}
.dark-mode-all .formulario fieldset {
  border: 1px solid #262626;
}
.dark-mode-all .formulario span, .dark-mode-all .formulario legend, .dark-mode-all .formulario label {
  color: #FFFFFF;
}
.dark-mode-all .formulario input:not([type=submit]),
.dark-mode-all .formulario textarea, .dark-mode-all .formulario select {
  background-color: #000000;
  border-color: #262626;
  color: #FFFFFF;
}

.alerta {
  display: block;
  margin-bottom: 1rem;
  width: 100%;
  text-align: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #FFFFFF;
}
.alerta:first-of-type {
  margin-top: 1rem;
}

.alerta-error {
  background-color: #ff0000;
}

.alerta-correcto {
  background-color: #71B100;
}

.no-wep .inicio {
  background-image: url(../../../build/img/header.jpg);
}

.webp .inicio {
  background-image: url(../../../build/img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 60rem;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 60rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  background-color: #333333;
}
.footer .contenedor-footer {
  padding-top: 2rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  padding-top: 2rem;
  padding-bottom: 2rem;
  margin-bottom: 0;
}

.footer.seccion {
  margin-bottom: 0;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 1rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.dark-mode {
  display: flex;
  padding-top: 2rem;
}
.dark-mode img {
  filter: invert(100%);
  width: 4rem;
}
.dark-mode img:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .dark-mode {
    justify-content: flex-end;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  margin-top: 1rem;
}
.navegacion a {
  color: #FFFFFF;
  font-size: 2.2rem;
  text-align: center;
}
.navegacion a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    column-gap: 2rem;
  }
}

.ocultar {
  display: none;
}

/* Mobile menu */
.mobile-menu {
  width: 100%;
  margin-top: 2rem;
}
.mobile-menu img {
  width: 4rem;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
@media (min-width: 992px) {
  .iconos-nosotros {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .anuncios {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
}
.anuncio .anuncio-contenido {
  padding: 2rem;
}
.anuncio h3 {
  margin: 0 auto;
}
.anuncio p {
  text-align: justify;
}
.anuncio .precio {
  color: #71B100;
  font-size: 3rem;
}
.anuncio .precio, .anuncio .caracteristicas-iconos p {
  font-weight: 700;
}

.caracteristicas-iconos {
  display: flex;
  column-gap: 1rem;
}
.caracteristicas-iconos li {
  flex: 1;
  display: flex;
  column-gap: 1rem;
}
.caracteristicas-iconos li img {
  flex: 0 0 1rem;
}

.boton-amarillo {
  background-color: #E08709;
  padding: 1rem 2rem;
  display: inline-block;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.9rem;
  border: none;
  cursor: pointer;
}
.boton-amarillo:hover {
  background-color: #af6907;
}

.boton-amarillo-block {
  background-color: #E08709;
  padding: 1rem 2rem;
  display: block;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.9rem;
  border: none;
  cursor: pointer;
}
.boton-amarillo-block:hover {
  background-color: #af6907;
}

.boton-verde {
  background-color: #71B100;
  padding: 1rem 2rem;
  display: inline-block;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.9rem;
  border: none;
  cursor: pointer;
}
.boton-verde:hover {
  background-color: #507e00;
}

.boton-verde-block {
  background-color: #71B100;
  padding: 1rem 2rem;
  display: block;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.9rem;
  border: none;
  cursor: pointer;
}
.boton-verde-block:hover {
  background-color: #507e00;
}

.boton-rojo {
  background-color: #ff0000;
  padding: 1rem 2rem;
  display: inline-block;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.9rem;
  border: none;
  cursor: pointer;
}
.boton-rojo:hover {
  background-color: #cc0000;
}

.boton-rojo-block {
  background-color: #ff0000;
  padding: 1rem 2rem;
  display: block;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.9rem;
  border: none;
  cursor: pointer;
}
.boton-rojo-block:hover {
  background-color: #cc0000;
}

.webp .imagen-contacto {
  background-image: url(../../../build/img/encuentra.webp);
}

.no-webp .imagen-contacto {
  background-image: url(../../../build/img/encuentra.jpg);
}

.imagen-contacto {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding: 10rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 3rem;
}
.imagen-contacto h2, .imagen-contacto p {
  margin: 0;
  color: #FFFFFF;
}
.imagen-contacto h2 {
  font-size: 4rem;
  font-weight: 700;
}
.imagen-contacto p {
  font-size: 1.8rem;
}

@media (min-width: 992px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

.blog h3, .testimoniales h3 {
  margin-top: 0;
}

.entrada-blog {
  margin-bottom: 2rem;
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
    align-items: center;
  }
}
.entrada-blog img {
  height: auto;
}

.entrada-texto h4, .entrada-texto p {
  margin-top: 0;
  margin-bottom: 1rem;
}
.entrada-texto h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
}
.entrada-texto a {
  color: #000000;
}
.entrada-texto span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  border-radius: 5%;
  font-size: 2.4rem;
  padding: 2rem 2rem 2rem 0.5rem;
  color: #FFFFFF;
}
.testimonial blockquote {
  padding-left: 6rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../../../build/img/comilla.svg);
  background-repeat: no-repeat;
  width: 5rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: 0;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.nosotros h4 {
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.nosotros p {
  margin-top: 0;
  margin-bottom: 0;
  line-height: 2;
}
@media (min-width: 992px) {
  .nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}

.propiedad {
  margin-bottom: 6rem;
}
.propiedad img {
  height: auto;
}
.propiedad .precio {
  font-size: 4rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
  color: #71B100;
}
.propiedad p {
  text-align: justify;
}

.caracteristicas-iconos-propiedad {
  column-gap: 2rem;
}
.caracteristicas-iconos-propiedad li {
  flex: unset;
}

.contacto img {
  height: auto;
}

.formulario {
  margin-top: 2rem;
}
.formulario input:not([type=file]), .formulario textarea, .formulario select {
  border-radius: 1rem;
}
.formulario input[type=submit] {
  border-radius: 0;
}
.formulario input[type=submit]:hover {
  cursor: pointer;
}
.formulario legend {
  font-size: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .campo {
  display: block;
  margin-bottom: 2rem;
}
.formulario .campo-inline {
  display: inline;
  padding-right: 2rem;
}
.formulario .campo-label {
  text-transform: uppercase;
  display: block;
  font-weight: 900;
}
.formulario .campo-label-inline {
  display: inline;
  padding-right: 1rem;
}
.formulario .campo-smaller {
  text-transform: none;
  font-weight: 400;
}
.formulario .campo-valor {
  width: 100%;
  border: 1px solid #e1e1e1;
  padding: 0.5rem;
}

.propiedades-listado {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
.propiedades-listado thead {
  background-color: #71B100;
}
.propiedades-listado thead th {
  color: #FFFFFF;
  padding: 2rem;
  font-size: 1.5rem;
}
.propiedades-listado tbody td {
  font-size: 1.8rem;
  font-weight: 700;
}
.propiedades-listado tbody img {
  width: 15rem;
  margin: 0 auto;
}
.propiedades-listado tbody a, .propiedades-listado tbody input {
  text-transform: capitalize;
}

.imagen-small {
  width: 20rem;
}